  .footer__navigation {
    border-top: 1px solid #777;
    padding-top: 2rem;
    display: flex;
    align-content: Ã§enter;
    align-items: center;
    text-align: center; }
    @media only screen and (max-width: 56.25em) {
      .footer__navigation {
        width: 100%;
